'use client';

import React, { useState } from 'react';
import MenuIcon from '@mui/icons-material/Menu';
import SearchIcon from '@mui/icons-material/Search';
import NotificationsIcon from '@mui/icons-material/Notifications';
import AccountCircleIcon from '@mui/icons-material/AccountCircle';
import LanguageIcon from '@mui/icons-material/Language';
import Badge from '@mui/material/Badge';
import IconButton from '@mui/material/IconButton';
import HomeIcon from '@mui/icons-material/Home';
import SettingsIcon from '@mui/icons-material/Settings';
import LogoutIcon from '@mui/icons-material/Logout';

export default function Dashboard() {
  const [sidebarOpen, setSidebarOpen] = useState(true);

  const toggleSidebar = () => {
    setSidebarOpen(!sidebarOpen);
  };

  return (
    <div className="flex h-screen bg-gray-100">
      {/* Sidebar */}
      <aside
        className={`transition-all duration-300 bg-gradient-to-b from-gray-800 to-black text-white ${
          sidebarOpen ? 'w-72' : 'w-20'
        } h-full shadow-lg`}
      >
        <div className="flex items-center justify-between p-4">
          <h1
            className={`text-2xl font-bold tracking-wide transition-opacity duration-300 ${
              !sidebarOpen && 'opacity-0'
            }`}
          >
            E-Commerce
          </h1>
          <IconButton onClick={toggleSidebar} color="inherit">
            <MenuIcon />
          </IconButton>
        </div>
        <nav className="mt-6">
          <ul className="space-y-2">
            <li>
              <a
                href="#"
                className="flex items-center gap-4 px-4 py-2 rounded-lg hover:bg-gray-700 transition-colors"
              >
                <HomeIcon />
                {sidebarOpen && <span className="font-medium">Dashboard</span>}
              </a>
            </li>
            <li>
              <a
                href="#"
                className="flex items-center gap-4 px-4 py-2 rounded-lg hover:bg-gray-700 transition-colors"
              >
                <SettingsIcon />
                {sidebarOpen && <span className="font-medium">Settings</span>}
              </a>
            </li>
            <li>
              <a
                href="#"
                className="flex items-center gap-4 px-4 py-2 rounded-lg hover:bg-gray-700 transition-colors"
              >
                <LogoutIcon />
                {sidebarOpen && <span className="font-medium">Logout</span>}
              </a>
            </li>
          </ul>
        </nav>
      </aside>

      {/* Main Content */}
      <main className={`flex-1 transition-all duration-300 ${sidebarOpen ? 'ml-72' : 'ml-20'}`}>
        {/* Header */}
        <header className="flex items-center justify-between bg-white p-4 shadow-md">
          {/* Sidebar Toggle Button */}
          <IconButton
            onClick={toggleSidebar}
            className="block md:hidden text-gray-700"
            color="inherit"
          >
            <MenuIcon />
          </IconButton>

          <div className="flex items-center bg-gray-200 px-3 py-1 rounded-lg w-96">
            <SearchIcon className="text-gray-600" />
            <input
              type="text"
              placeholder="Search..."
              className="bg-transparent focus:outline-none flex-1 ml-2"
            />
          </div>
          <div className="flex items-center space-x-4">
            <IconButton color="inherit">
              <Badge badgeContent={4} color="error">
                <NotificationsIcon />
              </Badge>
            </IconButton>
            <IconButton color="inherit">
              <LanguageIcon />
            </IconButton>
            <IconButton color="inherit">
              <AccountCircleIcon />
            </IconButton>
          </div>
        </header>

        {/* Main Content */}
        <section className="p-6">
          <h1 className="text-2xl font-bold">Welcome to your dashboard</h1>
          <p className="text-gray-600 mt-2">Manage your store efficiently and with ease.</p>
        </section>
      </main>
    </div>
  );
}
